import {
	PanelRow,
	ToggleControl,
	SelectControl,
	CheckboxControl,
	Spinner,
} from '@wordpress/components';
import { addFilter } from '@wordpress/hooks';
import { dispatch } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';


// Single Components for hook callbacks.
const BlockSettings = ( { allData } ) => {

	const { enable_gutenberg: enableGutenberg, enable_custom_template: enableCustomTemplate, selected_template: selectedTemplate, options } = allData;
	const { templates } = options;
	const { updateSettings } = dispatch( 'WPTravel/Admin' );

	// Local state.
	const initialState = {
		resetting:false,
		showMigrateCompleteNotice:false
	};
	const [ { resetting, showMigrateCompleteNotice }, setState ] = useState(initialState)
	const updateState = data => {
		setState(state => ({ ...state, ...data }))
	};
	setTimeout( () => {
		if ( 'undefined' !== typeof showMigrateCompleteNotice && showMigrateCompleteNotice ) {
			updateState( {
				showMigrateCompleteNotice:false
			} );
		}
	}, 5000 );
	// end of local state.
	return <div className="wp-travel-ui wp-travel-ui-card settings-blocks">
		<h2>{ __( 'Block Settings', 'wp-travel' ) }</h2>
		<PanelRow>
			<label>{ __( 'Enable Gutenberg', 'wp-travel' ) }</label>
			<div className="wp-travel-field-value">
				<ToggleControl
					checked={ enableGutenberg == 'yes' }
					onChange={ () => {
						updateSettings( {
							...allData,
							enable_gutenberg: 'yes' === enableGutenberg ? 'no' : 'yes'
						} );
					} }
				/>
				<p className="description">{ __( 'This will enable Gutenberg in trip edit page', 'wp-travel' ) }</p>
			</div>
		</PanelRow>
		{ 'yes' === enableGutenberg &&
			<>
				<PanelRow>
					<label>{ __( 'Enable Custom Template', 'wp-travel' ) }</label>
					<div className="wp-travel-field-value">
						<ToggleControl
							checked={ enableCustomTemplate == 'yes' }
							onChange={ () => {
								updateSettings( {
									...allData,
									enable_custom_template: 'yes' === enableCustomTemplate ? 'no' : 'yes'
								} );
							} }
						/>
						<p className="description">{ __( 'This will enable Default template in trip edit page', 'wp-travel' ) }</p>
					</div>
				</PanelRow>
				{ 'yes' === enableCustomTemplate &&
					<PanelRow>
						<label>{ __( 'Select Template', 'wp-travel' ) }</label>
						<div className="wp-travel-field-value">
							<SelectControl
								value={ selectedTemplate }
								options={ templates }
								onChange={ ( value ) => {
									updateSettings( {
										...allData,
										selected_template: value
									} );
								} }
							/>
							<p className="description">
								{ templates.length < 2 ? __( 'Please add your templates form \'WP Travel > Templates\'' ) : __( 'If you don\'t select template, default template will be used.', 'wp-travel' ) }
							</p>
						</div>
					</PanelRow>
				}

				<PanelRow>
					<label>{ __( 'Reset All trip templates', 'wp-travel' ) } { resetting && <Spinner /> } { showMigrateCompleteNotice && <p className="text-success" >All Templates reset successfully!!</p>}</label>
					<div className="wp-travel-field-value">
						<CheckboxControl
							onChange={() => {
								if (confirm('This will reset all trips template. Do you want to continue?')) {
									updateState({
										resetting: true
									})

									apiFetch({ url: `${ajaxurl}?action=wptravel_reset_templates&_nonce=${_wp_travel_admin._nonce}`, data: { reset_template: true }, method: 'post' }).then(res => {

										updateState({
											resetting: false,
											showMigrateCompleteNotice: true
										})
									});
								}
							}}
						/>
						<p className="description">{__('This will replace all trips with default template selected above. This action can not be undo.', 'wp-travel-blocks')}</p>
					</div>
				</PanelRow>
			</>
		}
	</div>;
};

// Callbacks.
const BlockSettingsCB = (content, allData) => {
	return [ ...content, <BlockSettings allData={ allData } key="BlockSettings" /> ];
};

// Hooks.
addFilter( 'wptravel_settings_tab_content_block_settings', 'WPTravelBlock/Settings/BlockSettings', BlockSettingsCB, 10 );
