import { addFilter } from '@wordpress/hooks';
import { __ } from '@wordpress/i18n';
import './blockSettings';

addFilter( 'wp_travel_settings_tabs', 'wp_travel', ( content ) => {
	content = [
		...content,
		{
			name: 'block-settings',
			title: __( 'Block Settings', 'wp-travel' ),
			className: 'tab-block-settings',
		},
	];
	return content;
} );
